
import { HashRouter as Router,Redirect, Route, Switch } from 'react-router-dom'

import './assets/scss/global.scss';
import { ContactIndex } from './views/ContactIndex'
import { StatisticPage } from './views/StatisticPage'
import { AppHeader } from './cmps/AppHeader'
import { ContactEdit } from './views/ContactEdit'
import { ContactDetails } from './views/ContactDetails'
import { Signup } from './views/SignupPage'
import { Home } from './views/Home'

import { userService } from './services/user.service'

function PrivateRoute(props) {
    const user = userService.getUser()
    return user ? <Route {...props} /> : <Redirect to='/signup' />
}


function App() {
    return (
        <Router>
        <div className="main-app">

        <AppHeader />

            <main className='main-container'>
                <Switch>
                        <Route path="/contact/edit/:id?" component={ContactEdit} />
                        <Route path="/contact/:id" component={ContactDetails} />
                        <PrivateRoute path="/ContactIndex" component={ContactIndex} />
                        <PrivateRoute path="/StatisticPage" component={StatisticPage} />
                        <Route path="/signup" component={Signup} />
                        <PrivateRoute path='/' component={Home} />
                    </Switch>
            </main>

            <footer>
                <section className='container'>
                    contactRights 2022 &copy;
                </section>
            </footer>

        </div>
        </Router>
    );
}

export default App;
